.blue {
  color: blue;
}

.red {
  color: red;
}

.green {
  color: green;
}


li {
  text-align: center;
  list-style-type: none;
  border: 1px solid #FFD700;
  width: 30%;
  margin: 4px auto;
}
:focus {
  outline: auto 1px #FFD700;
}
input {
  margin: 5px auto;
}
input.input {
  margin: 0px auto;
  border: solid 1px #FFD700;
  padding: 0;
  margin: 5px auto;
  width: 200px;
}

input:focus {
border: 2px solid #FFD700;
border: none;
padding: 0px;
}

p {
  margin: 0;
  font-size: 40px;
 
}

.coin {
  
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  width: 100%;
  height: 200px;
  font-size: 100px;
  
  align-items: center;
}

.div-input {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}